import { Stars } from "./Stars";

export default function Sky({ lowQuality }) {
  return (
    <Stars
      renderOrder={1}
      count={lowQuality ? 1000 : 3000}
      radius={4500}
      factor={lowQuality ? 100 : 200}
      fade={lowQuality ? false : true}
      saturation={1}
    />
  );
}
