import { useProgress } from "@react-three/drei";
import { useEffect } from "react";
import { ProgressContainer, Row } from "./ProgressStyle";
import CircularProgress from "@mui/material/CircularProgress";
import Dots from "./Dots";

export default function Progress({ onOpenLayer }) {
  const { active, progress } = useProgress();

  useEffect(() => {
    if (active === false && progress > 0) {
      setTimeout(() => {
        onOpenLayer();
      }, 300);
    }
  }, [progress, active, onOpenLayer]);

  return (
    <ProgressContainer>
      <CircularProgress
        variant="determinate"
        value={progress}
        size={80}
        style={{ color: "#FFFFFF" }}
      />
      <Row>
        LOADING <Dots />
      </Row>
    </ProgressContainer>
  );
}
