import { useCallback, useEffect, useState } from "react";
import Progress from "../Progress/Progress.js";
import QualityButton from "../QualityButton/QualityButton.js";

import {
  Info,
  QSBackground,
  QSButton,
  QSButtonsContainer,
  QSContainer,
  QSName,
  QSSubHeading,
  QSTitle,
  QSTop,
  TriangleTop,
  TriangleBottom,
  TriangleLeft,
  TriangleRight,
} from "./QualitySelectStyle.js";

export default function QualitySelect({ quality, setQuality }) {
  const [opacity, setOpacity] = useState(0);
  const [transition, setTransition] = useState("5s");
  const [top, setTop] = useState("65%");
  const [open, setOpen] = useState(false);
  const [showBackground, setShowBackground] = useState(true);
  const [cursor, setCursor] = useState(null);
  const [showProgress, setShowProgress] = useState(false);
  const [showQualityLayer, setShowQualityLayer] = useState(true);

  useEffect(() => {
    setOpacity(1);
    setTop(0);
  }, []);

  const onOpenLayer = useCallback(() => {
    setTransition("800ms");
    setOpacity(0);
    setShowBackground(false);
    setOpen(true);
    setShowProgress(false);
    setTimeout(() => {
      setShowQualityLayer(false);
    }, 800);
  }, []);

  const onSelectQuality = useCallback(
    (value) => {
      const removeOptionsContainer = () => {
        setTop("65%");
        setCursor("wait");
      };
      const initThree = () => {
        setQuality(value);
        setShowProgress(true);
      };

      removeOptionsContainer();
      setTimeout(() => {
        initThree();
      }, 450);
    },
    [setQuality]
  );

  const onResetQuality = useCallback(() => {
    setCursor(null);
    setShowQualityLayer(true);
    setOpen(false);
    setTimeout(() => {
      setQuality(null);
      setShowBackground(true);
      setOpacity(1);
      setTop(0);
    }, 800);
  }, [setQuality]);
  return (
    <>
      <TriangleTop open={open} cursor={cursor} />
      <TriangleBottom open={open} cursor={cursor} />
      <TriangleLeft open={open} cursor={cursor} />
      <TriangleRight open={open} cursor={cursor} />
      {showQualityLayer ? (
        <QSBackground showBackground={showBackground}>
          <QSTop opacity={opacity} transition={transition}>
            <QSName>DAVID BEALE</QSName>
            <QSSubHeading>PERSONAL PROJECTS</QSSubHeading>
          </QSTop>
          <Info>*Chrome recommended*</Info>
          <QSContainer top={top}>
            <QSTitle>Select Quality</QSTitle>
            <QSButtonsContainer>
              <QSButton cursor={cursor} onClick={() => onSelectQuality(1)}>
                LOW
              </QSButton>
              <QSButton cursor={cursor} onClick={() => onSelectQuality(0)}>
                HIGH
              </QSButton>
            </QSButtonsContainer>
          </QSContainer>
          {showProgress && <Progress onOpenLayer={onOpenLayer} />}
        </QSBackground>
      ) : (
        <QualityButton quality={quality} onResetQuality={onResetQuality} />
      )}
    </>
  );
}
