import { useTexture } from "@react-three/drei";
import { useFrame } from "@react-three/fiber";
import { useRef } from "react";
import sky from "./sky.jpg";
export default function Background({ stage }) {
  const matRef = useRef();
  const background = useTexture(sky);

  useFrame(() => {
    if (stage >= 2 && matRef.current.opacity < 1) {
      matRef.current.opacity += 0.001;
    }
  });
  return (
    <mesh position={[0, 0, -700]}>
      <planeBufferGeometry args={[1780, 1000, 1000]} />
      <meshBasicMaterial
        ref={matRef}
        map={background}
        transparent={true}
        opacity={0}
      />
    </mesh>
  );
}
