import { useRef, useState, Suspense } from "react";
import { Canvas } from "@react-three/fiber";
import { OrbitControls, Stats, Loader } from "@react-three/drei";
import { isBrowser } from "react-device-detect";

import Particles from "../Particles/Particles";
import ScrollDown from "../ScrollDown/ScrollDown";
import Sky from "../Sky/Sky";
import Content from "../Content/Content";
import Effects from "../Effects/Effects";
import NoEffects from "../NoEffects/NoEffects";
import Smoke from "../Smoke/Smoke";
import Rig from "../../Rig";
import Container from "../Container/Container";
import Background from "../Background/Background";
import { useSkip } from "./useSkip";

const debug = false;

export default function App({ lowQuality }) {
  const progress = useRef(2);
  const mouse = useRef([0, 0]);
  const mouseRange = useRef(5);
  const scroll = useRef(0);
  const swipingInProgress = useRef(false);
  const [stage, setStage] = useState(0);
  const [ready, setReady] = useState(false);

  const skip = useSkip(debug, lowQuality);
  return (
    <Container
      scroll={scroll}
      mouse={mouse}
      progress={progress}
      skip={skip}
      swipingInProgress={swipingInProgress}
      setStage={setStage}
    >
      <Canvas camera={{ position: [0, 0, 700], fov: 40, far: 10000 }}>
        <Suspense fallback={null}>
          {ready && !skip && stage <= 2 && (
            <>
              <Effects>
                <Particles
                  stage={stage}
                  setStage={setStage}
                  mouseRange={mouseRange}
                  progress={progress}
                />
              </Effects>
            </>
          )}
          <NoEffects>
            <Content
              scroll={scroll}
              stage={stage}
              debug={debug}
              skip={skip}
              swipingInProgress={swipingInProgress}
              ready={ready}
              setReady={setReady}
            />
            {isBrowser ? (
              <>
                <Stats className="stats" />
                <Sky lowQuality={lowQuality} />
                {!lowQuality && <Smoke stage={stage} />}
                <Rig mouse={mouse} mouseRange={mouseRange} />
              </>
            ) : (
              <Background stage={stage} />
            )}
          </NoEffects>
          {/* <OrbitControls /> */}
        </Suspense>
      </Canvas>
      {!isBrowser && <Loader />}
      {stage < Infinity && <ScrollDown />}
    </Container>
  );
}
