import styled from "styled-components";

export const Info = styled.div`
  position: fixed;
  color: white;
  left: 5px;
  top: 5px;
  font-size: 1rem;
  font-family: "Quicksand", sans-serif;
  z-index: 9999;
`;

export const QSBackground = styled.div`
  height: 100vh;
  width: 100vw;
  background: ${({ showBackground }) =>
    showBackground
      ? "linear-gradient(to top, #070c13 0%, #05263d 100%)"
      : "transparent"};
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-family: "Quicksand", sans-serif;
  overflow: hidden;
`;
export const QSTop = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 40px;
  transition: ${({ transition }) => transition};
  opacity: ${({ opacity }) => opacity};
`;
export const QSName = styled.div`
  font-size: 3rem;
`;
export const QSSubHeading = styled.div`
  font-size: 1.8rem;
`;
export const QSContainer = styled.div`
  width: 350px;
  border-radius: 25px;
  padding: 16px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: white 0px 0px 15px;
  position: relative;
  transition: top 1000ms cubic-bezier(0.075, 0.82, 0.165, 1);
  top: ${({ top }) => top};
`;
export const QSTitle = styled.div`
  font-size: 1.6rem;
`;
export const QSButtonsContainer = styled.div`
  display: flex;
  margin-top: 15px;
`;
export const QSButton = styled.div`
  box-shadow: white 0px 0px 5px;
  color: white;
  margin: 10px;
  padding: 15px;
  border-radius: 10px;
  width: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: ${({ cursor }) => (cursor ? cursor : "pointer")};
  font-weight: 900;
  transition: background-position 0.2s ease-in-out;
  background-image: linear-gradient(to top, white 50%, transparent 50%);
  background-size: 100% 200%;
  background-position: top;
  &:hover {
    background-position: bottom;
    color: black;
  }
`;
const Triangle = styled.div`
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, #070c13 0%, #05263d 100%);
  transition: all 0.8s cubic-bezier(0.645, 0.045, 0.355, 1);
  cursor: ${({ cursor }) => (cursor ? cursor : "")};
`;
export const TriangleTop = styled(Triangle)`
  clip-path: ${({ open }) =>
    open
      ? "polygon(50% 0, 100% 0, 100% 50%, 100% 0)"
      : "polygon(50% 0, 50% 50%, 100% 50%, 100% 0)"};
`;
export const TriangleBottom = styled(Triangle)`
  clip-path: ${({ open }) =>
    open
      ? "polygon(0 50%, 0 100%, 50% 100%, 0 100%)"
      : "polygon(0 50%, 50% 50%, 50% 100%, 0 100%)"};
`;
export const TriangleLeft = styled(Triangle)`
  clip-path: ${({ open }) =>
    open
      ? "polygon(0 0, 50% 0, 0 0, 0 50%)"
      : "polygon(0 0, 50% 0, 50% 50%, 0 50%)"};
`;
export const TriangleRight = styled(Triangle)`
  clip-path: ${({ open }) =>
    open
      ? "polygon(100% 100%, 100% 50%, 100% 100%, 50% 100%)"
      : "polygon(50% 50%, 100% 50%, 100% 100%, 50% 100%)"};
`;
